.container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  .backBtn {
    color: white;
    padding: .5rem;
    font-size: 1.2rem;
    cursor: pointer;
  }

  .cards {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    overflow-y: auto;

    > * {
      margin: 1rem;
    }
  }
}
